/**
 * @description: -
 * @param {String} str 
 * @param {String} mark -
 * @return {String}
 * @example: toHump('border-radius')  // borderRadius
 */
export default function toHump(str = '', mark = '-') {
  return str.replace(new RegExp(`${mark}(\\w)`, 'g'), function (all, letter) {
    return letter.toUpperCase();
  });
}
