/**
 * @description: 
 * @param {function} fn 
 * @param {number} wait  5000
 * @param {boolean} immediate false
 * @return {object} {start:Function,stop:Function}
 * @example createTimer(()=>console.log(''))
 */
export default function createTimer(fn, wait = 5000, immediate = false) {
  let timerId = null;
  let isStop = true;

  const start = function () {
    if (isStop) {
      isStop = false;
      fn(next, stop);
    }
    return this;
  };
  const next = function () {
    if (!isStop) {
      stop();
      timerId = setTimeout(start, wait);
    }
  };
  const stop = function () {
    isStop = true;
    if (timerId) {
      clearTimeout(timerId);
      timerId = null;
    }
    return this;
  };

  if (immediate) start();

  return { start, stop };
}
