/**
 * @description: 
 * @param {Function} func 
 * @param {Number} wait 
 * @param {{leading:Boolean,trailing:Boolean}} options
 * @return {Function}
 * @example const func = throttle(()=>console.log(1),100);
 */
export default function throttle(func, wait = 100, options) {
  let timeout, context, args, result;

  // 
  let previous = 0;

  //  options 
  if (!options) options = {};

  const later = function () {
    //  { leading: false } 
    //  previous  0
    // 
    previous = options.leading === false ? 0 : Date.now();

    //  null  !timeout  timeout
    timeout = null;

    // 
    result = func.apply(context, args);
    if (!timeout) context = args = null;
  };

  // 
  //  func
  // func 
  function throttled() {
    // 
    const now = Date.now();

    //  previous  0
    //  { leading: false }
    //  previous 
    if (!previous && options.leading === false) previous = now;

    //  func 
    const remaining = wait - (now - previous);
    context = this;
    args = arguments;

    // remaining <= 0
    //  {leading: false}
    //  previous  0wait - (now - previous)  <= 0
    //  previous  now
    if (remaining <= 0 || remaining > wait) {
      if (timeout) {
        clearTimeout(timeout);

        // clearTimeout(timeout)  timeout  null
        // 
        timeout = null;
      }

      //  previous 
      previous = now;

      //  func 
      result = func.apply(context, args);
      if (!timeout) context = args = null;
    } else if (!timeout && options.trailing !== false) {
      // 
      //  if 
      //  {trailing: false}
      //  remaining milliseconds  later 
      timeout = setTimeout(later, remaining);
    }
    return result;
  }

  // 
  throttled.cancel = function () {
    clearTimeout(timeout);
    previous = 0;
    timeout = context = args = null;
  };

  return throttled;
}
