/**
 * @description: 
 * @param {string|number} val
 * @return {boolean}
 */
function isInteger(val) {
  return /^[1-9]\d*$/.test(val);
}

/**
 * @description: 
 * @param {string} val
 * @return {boolean}
 */
function isPhone(val) {
  return /^1[3456789]\d{9}$/g.test(val);
}

/**
 * @description: 
 * @param {string} val
 * @return {boolean}
 */
function isEmail(val) {
  return /^([a-zA-Z0-9]+[_|_|\-|.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|_|.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,6}$/.test(val);
}

/**
 * @description: 
 * @param {string} val
 * @return {boolean}
 */
function hasHost(val) {
  return /^(https|http|\/{2}|ftp|rtsp|mms)/.test(val);
}

/**
 * @description: 
 * @param {string} val
 * @return {boolean}
 */
function hasEmoj(val) {
  return /[^\u0020-\u007E\u00A0-\u00BE\u2E80-\uA4CF\uF900-\uFAFF\uFE30-\uFE4F\uFF00-\uFFEF\u0080-\u009F\u2000-\u201f\u2026\u2022\u20ac\r\n]/g.test(val);
}

/**
 * @description: 
 * @param {string} idCard
 * @return {boolean}
 */
function isIdCard(idCard) {
  // 1518
  const regIdCard = /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/;
  // 
  if ((idCard = String(idCard)) && regIdCard.test(idCard)) {
    if (idCard.length === 18) {
      // 17
      const idCardWi = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
      // 1111
      const idCardY = [1, 0, 10, 9, 8, 7, 6, 5, 4, 3, 2];
      // 17
      let idCardWiSum = 0;
      for (let i = 0; i < 17; i++) {
        idCardWiSum += idCard.substring(i, i + 1) * idCardWi[i];
      }
      const idCardMod = idCardWiSum % 11; // 
      const idCardLast = idCard.substring(17); // 
      // 210X
      if (idCardMod === 2) {
        if (idCardLast == 'X' || idCardLast == 'x') {
          return true;
        }
      } else {
        // 
        if (idCardLast == idCardY[idCardMod]) {
          return true;
        }
      }
    }
  }
  return false;
}

/**
 * @description: 
 * @param {string} name
 * @param {number} length
 * @return {boolean}
 */
function isNameZh(name, length = 2) {
  return new RegExp(`/^[\u4e00-\u9fa5]{${length},}$/`).test(name);
}

/**
 * @description:  H/M + 106 H1234567890
 * @param {string} card
 * @return {boolean}
 */
function isHKCard(card) {
  return /^([A-Z]\d{6,10}(\(\w{1}\))?)$/.test(card);
}

/**
 * @description:  818   + 10 12345678  1234567890B
 * @param {string} card
 * @return {boolean}
 */
function isTWCard(card) {
  return /^(\d{8}|[a-zA-Z0-9]{10}|\d{18})$/.test(card);
}

/**
 * @description: 
 * @param {string} val
 * @return {boolean}
 */
function isPassport(val) {
  return /(^[CcTtEeKkGgDdSsPpHh]\d{8}$)|(^(([Ee][a-fA-F])|([DdSsPp][Ee])|([Kk][Jj])|([Mm][Aa])|(1[45]))\d{7}$)/.test(val);
}

/**
 * @description:  ////// + "" + 48 + "" 2001988, P011816X
 * @param {string} card
 * @return {boolean}
 */
function isOfficerCard(card) {
  return /^[\u4E00-\u9FA5]()([0-9a-zA-Z]{4,8})(?)$/.test(card);
}

const validate = {
  isInteger,
  isPhone,
  isEmail,
  hasHost,
  hasEmoj,
  isIdCard,
  isNameZh,
  isHKCard,
  isTWCard,
  isPassport,
  isOfficerCard,
};

export default validate;
