import isValidDate from './isValidDate';
import parseDate from './parseDate';
import parseSeconds from './parseSeconds';
import formatDate from './formatDate';

/**
 * @description: 
 * @param {Date|Number|String} date
 * @param {Date|Number|String} now 
 * @param {Number} maxDays
 * @param {String} nowStr ''
 * @return {String}
 * @example formatDiffTime(date, now, maxDays = 7, nowStr = '')
 */
export default function formatDiffTime(date, now, maxDays = 7, nowStr = '') {
  now = now || new Date();
  maxDays = maxDays || 7;
  if (!isValidDate((date = parseDate(date))) || !isValidDate((now = parseDate(now)))) return '';
  const diff = Math.floor((now.getTime() - date.getTime()) / 1000);
  if (diff === 0) return nowStr;
  const suffix = diff > 0 ? '' : '';
  const { d, h, m, s } = parseSeconds(Math.abs(diff));
  if (d > maxDays) {
    return formatDate(date, 'YYYY-MM-DD');
  }
  if (d) {
    return d + `${suffix}`;
  } else if (h) {
    return h + `${suffix}`;
  } else if (m) {
    return m + `${suffix}`;
  } else if (s) {
    return s + `${suffix}`;
  }
}
