import getTypeValue from '../../utils/getTypeValue';
import extend from '../../utils/extend';

let timer = 0;

// 
const toastOptions = {
  type: '', // class
  className: '', // class
  background: '', // 
  color: '', // 
  position: 'center', // center|top|bottom
  animate: 'fade', // 
  animateDuration: 300, // 
  message: '', // 
  icon: '', // 
  iconStyle: '', // 
  mask: true, // 
  maskOpacity: 0, // 
  duration: 2000, // 
  zIndex: 99999, // z-index 
  custom: '', // 
  success() { }, // callback
};

function getOptions(options, defaultOptions = toastOptions) {
  return extend(true, {}, defaultOptions, getTypeValue(options, Object, { message: options }));
}

const state = {
  isInit: false,
  visible: false,
  ...getOptions(),
};

/**
 * @description: toast
 * @param {String|toastOptions} options
 * @param {Function?} cb 
 * @return {void}
 */
function toast(options, cb) {
  options = getOptions(options);
  const beforeVisible = state.isInit && state.visible;
  extend(state, options, {
    isInit: true,
    visible: true,
  });
  clearTimeout(timer);
  if (options.duration > 0) {
    const duration = (!beforeVisible && options.animate ? options.animateDuration : 0) + options.duration;
    timer = setTimeout(() => {
      state.visible = false;
      typeof cb === 'function' && cb();
    }, duration);
  }
  options.success();
}

/**
 * @description: toast
 * @param {String} type
 * @return {void}
 */
function hide(type = '') {
  if (!type || typeof type !== 'string' || state.type === type) {
    clearTimeout(timer);
    state.visible = false;
  }
}

/**
 * @description: toast
 * @return {void}
 */
function clear() {
  clearTimeout(timer);
  state.isInit = false;
  state.visible = false;
}

/**
 * @description: loading
 * @param {string|toastOptions} options
 * @param {Function?} cb 
 * @return {void}
 */
function loading(options, cb) {
  toast(getOptions(options, loading.defaultOptions), cb);
}

loading.defaultOptions = {
  type: 'loading',
  icon: 'loading',
  duration: 0,
};

/**
 * @description: toast
 * @param {string|toastOptions} options
 * @param {Function?} cb 
 * @return {void}
 */
function success(options, cb) {
  toast(getOptions(options, success.defaultOptions), cb);
}

success.defaultOptions = {
  type: 'success',
  icon: 'success',
};

/**
 * @description: toast
 * @param {string|toastOptions} options
 * @param {Function?} cb 
 * @return {void}
 */
function fail(options, cb) {
  toast(getOptions(options, fail.defaultOptions), cb);
}

fail.defaultOptions = {
  type: 'fail',
  icon: 'fail',
};

toast.defaultOptions = toastOptions;
toast.state = state;
toast.loading = loading;
toast.success = success;
toast.fail = fail;
toast.hide = hide;
toast.clear = clear;

export default toast;
