import getTypeValue from '../../utils/getTypeValue';
import extend from '../../utils/extend';

// 
const dialogOptions = {
  className: '', // class
  animate: 'zoom-in', // 
  animateDuration: 300, // 
  maskOpacity: '', // 
  title: '', // 
  titleColor: '', // 
  message: '', // 
  messageColor: '', // 
  showCancel: true, // 
  cancelText: '', // 
  cancelColor: '', // 
  confirmText: '', // 
  confirmColor: '', // 
  zIndex: 9999, // z-index 
  success({ confirm, cancel }) { }, // callback
  custom: '', // 
};

function getOptions(options, defaultOptions = dialogOptions) {
  options = extend(true, {}, defaultOptions, options, getTypeValue(options, Object, { message: options }));
  if (typeof options.content !== 'undefined') options.message = options.content;
  return options;
};

// 
const handler = {
  reject() { },
  resolve() { },
  success() { },
};

const state = {
  isInit: false,
  visible: false,
  ...getOptions(),
  onCancel() {
    hide();
    handler.success && handler.success({ confirm: false, cancel: true });
    handler.reject && handler.reject();
  },
  onConfirm() {
    hide();
    handler.success && handler.success({ confirm: true, cancel: false });
    handler.resolve && handler.resolve();
  },
};

/**
 * @description: dialog
 * @param {string|dialogOptions} options
 * @return {promise}
 */
function dialog(options) {
  options = getOptions(options);
  return new Promise((resolve, reject) => {
    extend(handler, { reject, resolve, success: options.success });
    extend(state, options, {
      isInit: true,
      visible: true,
    });
  });
}

/**
 * @description: dialog
 * @return {void}
 */
function hide() {
  state.visible = false;
}

/**
 * @description: dialog
 * @return {void}
 */
function clear() {
  state.isInit = false;
  state.visible = false;
}

dialog.defaultOptions = dialogOptions;
dialog.state = state;
dialog.hide = hide;
dialog.clear = clear;

export default dialog;
