import { cureBaseUrl } from '../common/config.js'

export function selectIdByAccount(Account){
	return new Promise(function(reslove, reject){
		uni.request({
			url: `${cureBaseUrl}/selectIdByAccount`,
			method: 'GET',
			data: Account,
			success: res=> {
				let result =res;
				console.log('uid', res, result.sratusCode);
				reslove(result);
			},
			fail: () => {},
			complete: () => {}
		})
	})
	
}

