export function saveQuestion(data) {
	console.log(123)
	console.log(data)
	return new Promise(function(reslove, reject) {
		var token = uni.getStorageSync('token');
		uni.request({
			url: `${cureBaseUrl}/saveQuestion`,
			header: {
				"authorization": token
			},
			method: 'POST',
			data: data,
			fail: () => {},
			complete: () => {}
		})
	})
}

