const defaultOptions = {
  multipleSlots: true,
  addGlobalClass: true,
  virtualHost: true,
  styleIsolation: 'shared',
};

/**
 * @description: options
 * @param {defaultOptions} options
 * @return {defaultOptions}
 */
export default function getComponentOptions(options) {
  const {
    multipleSlots,
    addGlobalClass,
    virtualHost,
    styleIsolation,
  } = Object.assign({}, defaultOptions, options);

  return {

    multipleSlots, //  slot
    addGlobalClass, //   styleIsolation: apply-shared
    virtualHost, //  Vue flex  flex 
    styleIsolation, //   wxss  wxss  apply-shared  shared 

  };
}
